import React, { Component } from "react";

import data from "../articles/articles.json";
import clockIcon from "../assets/clock.png";

import { clock, date as dateStyle, leftSnippet, rightSnippet, snippetTitleStyle, description, articleProps } from "./styles";

export function ArticleSnippetList() {
    return (
        <div>
            {data.map((article, index) => {
                return (
                    <ArticleSnippet
                        key={index}
                        index={index}
                        article={article}
                    />
                );
            })}
        </div>
    );
}

class ArticleSnippet extends Component {
    constructor(props) {
        super(props);
        this.article = props.article;
        this.index = props.index;
        this.state = { hovered: false, zIndex: 1 };
    }

    handleClick() {
        window.location.href = "/#/article/" + this.article.id;
    }

    render() {
        const orientationLeft = this.index % 2 === 0;
        const snippetStyle = orientationLeft ? leftSnippet : rightSnippet

        return (
            <div
                onClick={() => this.handleClick()}
                onMouseOut={() => this.setState({ hovered: false, zIndex: 1 })}
                onMouseOver={() =>
                    this.setState({ hovered: true, zIndex: 100 })
                }
                style={{
                    transform: `${
                        this.state.hovered ? "scale(1.05)" : "scale(1)"
                    }`,
                    zIndex: `${this.state.zIndex}`,
                    WebkitTransition: "transform .2s ease-in-out",
                    cursor: "pointer",
                    ...snippetStyle
                }}
            >
                <p style={snippetTitleStyle}>{this.article.title}</p>
                <ArticleProperties
                    date={this.article.date}
                    minutesToRead={this.article.minutesToRead}
                />
                {this.article.snippet && (
                    <span style={description}>
                        {this.article.snippet}
                    </span>
                )}
            </div>
        );
    }
}

function ArticleProperties(props) {
    const { minutesToRead, date } = props;
    const MINUTES_PER_READING_CICLE = 10;
    const readingCicles = Math.ceil(minutesToRead / MINUTES_PER_READING_CICLE);

    let clocks = [];
    for (let i = 0; i < readingCicles; i++) {
        clocks.push(<Clock key={i} />);
    }

    return (
        <div>
            <span style={dateStyle}>{date}</span>
            &nbsp; &bull; &nbsp;
            {clocks}
            &nbsp;
            <span style={articleProps}>{minutesToRead} min read</span>
        </div>
    );
}

function Clock() {
    return <img src={clockIcon} alt="Logo" style={clock} />;
}
