import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";

import { ArticleSnippetList } from "./ArticleList";

import { header, portrait, circleAvatar, name, role, headerTitle } from "./styles";
import avatar from "../assets/avatar.png";

class Home extends Component {
    render() {
        return (
            <Container className="Row">
                <Row>
                    <Col xs="0" sm="0" md="1" lg="2"></Col>
                    <Col xs="12" sm="12" md="10" lg="8">
                        <Header />
                        <ArticleSnippetList />
                    </Col>
                    <Col xs="0" sm="0" md="1" lg="2"></Col>
                </Row>
            </Container>
        );
    }
}

function Header() {
    return (
        <div style={header}>
            <div>
                <div style={portrait}>
                    <img src={avatar} style={circleAvatar} alt="Avatar" />
                </div>
                <div style={headerTitle}>
                    <span style={name}>George Tintareanu</span>
                    <br />
                    <span style={role}>Software Developer</span>
                </div>
            </div>
        </div>
    );
}

export default Home;
