import React, { Component, Suspense } from "react";
import { Container, Row, Col } from "react-bootstrap";

import ErrorBoundary from "./ErrorBoundary";

class Article extends Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.articleId = this.articleIdFromHash(window.location.hash);
        this.innerComponent = React.lazy(() =>
            import(
                "../articles/article-" + this.articleId + "/" + this.articleId
            )
        );
    }

    articleIdFromHash(hash) {
        return hash.split("/")[2];
    }

    render() {
        return (
            <Container className={Row} style={{marginTop: "2vh", marginBottom: "2vh"}}>
                <Row>
                    <Col xs="0" sm="2" md="1"></Col>
                    <Col xs="12" sm="8" md="10">
                        <ErrorBoundary>
                            <Suspense fallback={<div>Loading...</div>}>
                                <this.innerComponent></this.innerComponent>
                            </Suspense>
                        </ErrorBoundary>
                    </Col>
                    <Col xs="0" sm="2" md="1"></Col>
                </Row>
            </Container>
        );
    }
}

export default Article;
