const colorsText = {
    header: "#D4E2E4",
};

export const header = {
    minHeight: "20vh",
    paddingTop: "7vh",
    paddingBottom: "7vh",
    display: "flex",
    flexWrap: "wrap",
};

export const circleAvatar = {
    borderRadius: "50%",
    backgroundColor: "rgb(238,176,163)",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center center",
    backgroundSize: "cover",
    width: "calc(30px + (100 - 30) * ((100vw - 100px) / (1600 - 500)))",
    height: "calc(30px + (100 - 30) * ((100vw - 100px) / (1600 - 500)))",
    boxShadow: "0 3px 2px rgba(0, 0, 0, 0.3)"
};

export const portrait = {
    margin: "3px 3px 3px 3px",
    verticalAlign: "middle",
    display: "inline-block",
    paddingRight: "2vh",
};

export const headerTitle = {
    verticalAlign: "middle",
    display: "inline-block",
    paddingLeft: "2vh",
};

export const name = {
    fontFamily: "Verdana, sans-serif",
    fontSize: "calc(18px + (42 - 18) * ((100vw - 100px) / (1600 - 300)))",
    color: "#843b62",
};

export const role = {
    fontStyle: "italic",
    fontSize: "calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)))",
    color: "#cfe4e5",
};

export const clock = {
    height: "18px",
    width: "18px",
    verticalAlign: "middle",
};

export const snippetTitleStyle = {
    color: "#db947d",
    fontFamily: "Verdana, sans-serif",
    borderBottom: "1px solid #503a40",
    fontSize: "calc(16px + (32 - 16) * ((100vw - 300px) / (1600 - 300)))",
};

export const snippet = {
    padding: "10px",
    marginBottom: "30px",
    width: "90%",
    clear: "left",
    color: "#D4E2E4",
};

export const leftSnippet = {
    ...snippet,
    boxShadow: "10px 5px #7d5d59",
    float: "left",
};

export const rightSnippet = {
    ...snippet,
    boxShadow: "-10px 5px #7d5d59",
    float: "right",
};

export const justify = {
    textAlign: "justify",
    textJustify: "inter-word",
};

export const description = {
    color: "rgb(207,228,229)",
    fontSize: "calc(12px + (22 - 12) * ((100vw - 300px) / (1600 - 300)))",
};

export const articleProps = {
    fontStyle: "italic",
    verticalAlign: "middle",
    fontSize: "calc(10px + (16 - 10) * ((100vw - 300px) / (1600 - 300))",
};

export const date = {
    fontStyle: "italic",
    verticalAlign: "middle",
};
